.text-input-input {
  position: relative;
}
.text-input-text {
  color: var(--dl-color-primary-dark500);
  height: auto;
  font-size: 12px;
  align-self: stretch;
  font-style: Medium;
  text-align: left;
  font-family: "Poppins";
  font-weight: 500;
  line-height: normal;
  font-stretch: normal;
  text-decoration: none;
}
.text-input-input1 {
  gap: 12px;
  width: 328px;
  height: 40px;
  border-color: var(--dl-color-primary-input-border);
  background-color: var(--dl-color-gray-white);
}






























